html{
    scroll-behavior: smooth;
}


.mainpage{
    display: flex;
    flex-direction: column;
    width: 90%;
    margin: auto;
    
    background-color: rgb(103, 154, 199);
}
@media only screen and (max-width: 1024px){
.mainpage{
    width: 90%;
}
}
@media only screen and (max-width: 512px){
    .mainpage{
    width: 100%;

    }
}