
.about{
    margin: 32px 0px 15px 0px;
    font-size: 18px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
    padding: 4px;

}


.about-main{
    display: flex;
    flex-direction: row;
    
    margin: 24px;
    justify-content: space-between;

}
.about-head{
    width: 30%;
    padding: 20px;

    /* font-size:larger; */
}
.about-bottom{
    width: 60%;
    /* font-size:larger; */
    align-items: center;
    text-align: center;
}
.about-heading{
    text-align: center;
    align-items: center;
}
@media only screen and (max-width: 720px){
    .about{
        margin:2px;
        font-size: 18px;
    }
    .about-main{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .about-head{
        width: 70%;
        padding: 4px;
        /* color: white; */
        margin-bottom: 10px;
        
    }
    .about-bottom{
        width: 90%;
        
        align-items: center;
        
    }
}