.skill-card{
    width: max-content;
    padding: 8px 16px;
    height: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 6px;
    border-radius: 8px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
    
    
    
}
.skill-icon{
    font-size: 80px;
    
}
.skill-name{
    font-size: 18px;
}
@media only screen and (max-width: 720px){
    .skill-card{
        width: max-content;
        height: 120px;
        
    }
    .skill-icon{
        font-size: 60px;
    }
}