.project-card{
    display: flex;
    flex: 1;
    margin: 16px 0px;
    padding: 8px 2px 4px 2px;
}
.project-photo{
    width: 40%;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;

}
.project-info{
    margin: 24px;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;

}
.project-title{
    font-size: 20px;
    font-weight: 600;
}
.project-tags{
    display: flex;
    max-width: 60%;
    
    flex-wrap:wrap ;
}
.tag{
   background-color: rgb(172, 95, 203);
   padding: 4px 8px;
   margin: 4px;
   margin-top: 10px;
   border-radius: 4px;
}
.project-links a{
    color: black;
    text-decoration: none;
    
}
.project-links{
    display: flex;
    margin-top: 7px;
}
.link-button{
    display: flex;
    align-items: center;
    justify-content: center;
   background-color: rgb(172, 95, 203);
    margin-top: 10px;
    width: max-content;
    padding: 4px 8px;
    border-radius: 4px;
    margin-right: 6px;
    
}

.link-button i{
    display: flex;
    align-items: center;
    mask-repeat: 4px;
}

@media only screen and (max-width: 720px){
    .project-photo{
        width: 90%;
        margin: auto;
        border-radius: 4px;
      box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;

    }
    
    .project-card{
        flex-direction: column-reverse;
        }
        .project-info{
            margin-left: 0px;
            margin-top: 12px;
            box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;

        }
        .project-tags{
            max-width: 100%;
        }
}
